import { Card } from '@mui/material'
import axios from 'axios'
import { useSnackbar } from 'notistack'
import { useLocation } from 'react-router-dom'
import { Conference } from 'src/models/Conference'
import AttendanceTable from './AttendanceTable'

function AttendanceList() {
  const location = useLocation()
  const { enqueueSnackbar } = useSnackbar()

  const handleAttendanceCheck = async (conferenceId: number, userId: number, isAttendance: boolean): Promise<void> => {
    try {
      const { data } = await axios.put(`${process.env.REACT_APP_APIHOST}/club/attendance`, {
        conferenceId: conferenceId,
        userId: userId,
        attendanceType: isAttendance
      })

      if (data.message !== 'ok') {
        enqueueSnackbar('출석 체크 중 오류가 발생했습니다. 다시 시도해주십시오.', { variant: 'error' })
      }
    } catch (e) {
      enqueueSnackbar('출석 체크 오류가 발생했습니다. 다시 시도해주십시오.', { variant: 'error' })
    }
  }

  return (
    <Card>
      <AttendanceTable conference={location.state as Conference} handleAttendanceCheck={handleAttendanceCheck} />
    </Card>
  )
}

export default AttendanceList
